/*
* cv page
*/
.cvContainer {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 22px;
}
.cvContainer .baseLine {
  width: 100%;
  height: 4px;
  background: #c0504d;
}
.cvContainer .cvContent {
  width: 634px;
  height: 990px;
  background: #ffffff;
  padding: 42px 70px;
}
.cvContainer .cvContent .heading {
  text-transform: uppercase;
  font-size: 15px;
  font-weight: bold;
}
.cvContainer .cvContent .personalDetail {
  display: -ms-flexbox;
  display: flex;
}
.cvContainer .cvContent .personalDetail .contents {
  -ms-flex: 2.2 1;
      flex: 2.2 1;
  margin: 10px 0 10px 0;
}
.cvContainer .cvContent .personalDetail .contents .details {
  display: -ms-flexbox;
  display: flex;
  margin-top: 4px;
}
.cvContainer .cvContent .personalDetail .contents .labels {
  -ms-flex: 1 1;
      flex: 1 1;
}
.cvContainer .cvContent .personalDetail .contents .values {
  -ms-flex: 1.3 1;
      flex: 1.3 1;
}
.cvContainer .cvContent .personalDetail .contents .label,
.cvContainer .cvContent .personalDetail .contents .value {
  margin-top: 4px;
  font-size: 12px;
}
.cvContainer .cvContent .personalDetail .avatar {
  -ms-flex: 1 1;
      flex: 1 1;
}
.cvContainer .cvContent .personalDetail .avatar img.normal {
  width: 188px;
  height: 188px;
  float: right;
}



