/*
* add cover letter
*/
.transitionAll,
.coverLetter .addCoverLetter,
.coverLetter .addCoverLetter .title,
.coverLetter .addCoverLetter .description {
  -webkit-transition: all linear 0.2s;
  -o-transition: all linear 0.2s;
  transition: all linear 0.2s;
}

.coverLetter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 22px;
}
.coverLetter .addCoverLetter {
  width: 450px;
  height: 110px;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
  text-align: center;
}
.coverLetter .addCoverLetter:hover {
  background: #fff;
  cursor: pointer;
  border-color: #288ad3;
}
.coverLetter .addCoverLetter .title {
  font-weight: bold;
  color: #000;
}
.coverLetter .addCoverLetter .description {
  color: #838383;
  font-size: 14px;
  margin-top: 2px;
}
.coverLetter .addCoverLetter:hover .title,
.coverLetter .addCoverLetter:hover .description {
  color: #288ad3;
}
.coverLetter .addCoverLetter:hover .cv-add {
  background-position: -650px -100px;
}
.coverLetter .addCoverLetter .cv-add {
  margin-top: 4px;
}



