@import url('https://fonts.googleapis.com/css?family=Poppins');
.cv-icon,
.cv-icon-lang {
  background: url('/assets/icon/cv-icon.png') top left;
  background-size: 980px auto;
  background-position: 0 -100px;
  width: 50px;
  height: 50px;
  display: inline-block;
}

.cv-icon.cv-new {
  background-position: 0 -100px;
}

.cv-icon.cv-upload {
  background-position: -50px -100px;
}

.cv-icon.cv-form {
  background-position: -100px -100px;
}

.cv-icon.cv-language {
  background-position: -150px -100px;
}

.cv-icon.cv-photo {
  background-position: -200px -100px;
}

.cv-icon.cv-signature {
  background-position: -250px -100px;
}

.cv-icon.cv-additional {
  background-position: -300px -100px;
}

.cv-icon.cv-download {
  background-position: -350px -100px;
}

.cv-icon.cv-account {
  background-position: -400px -100px;
}

.cv-icon.cv-add {
  background-position: -450px -100px;
}

.cv-icon.cv-photo-plus {
  background-position: -550px -100px;
}

.cv-icon.cv-close {
  background-position: -600px -100px;
}

.cv-icon.cv-add-active {
  background-position: -650px -100px;
}

/* language */
.cv-icon-lang {
  background-position: 0 -150px;
  width: 25px;
  height: 25px;
}

.cv-icon-lang.icon-vietnam {
  background-position: -125px -150px;
}

.cv-icon-lang.icon-germany {
  background-position: -150px -150px;
}

.cv-icon-lang.icon-korea {
  background-position: -175px -150px;
}

.cv-icon-lang.icon-japan {
  background-position: -200px -150px;
}

* {
  margin: 0 auto;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

.App {
  width: 100%;
  height: 100%;
  background: #f6f6f6;
}



